import Guid from '../guid/Guid';

/**
 * Object that represents the data neccesary to build an SPSite object.
 *
 * @internal
 */
export interface ISPSiteData {
  id: string;
}

/**
  * This class is primarily used with the PageContext class.  It provides contextual
  * information for the SharePoint site collection ("site") that hosts the page.
  *
  * @public
  */
export default class SPSite {
  private _id: Guid;

  /**
   * @internal
   */
  constructor(data: ISPSiteData) {
    this._id = Guid.tryParse(data.id);
  }

  /**
   * The GUID that identifies the SPSite on the server. This property could be undefined
   * if the information isn't avalible.
   */
  public get id(): Guid {
    return this._id;
  }
}
